.mlp-quicklinks {
    display: flex;

    &--top-right,
    &--bottom-right {
        justify-content: flex-end;
    }

    /*
     * Form UI
     */
    .mlp-quicklinks-form {
        &__label,
        &__select,
        &__submit {
            display: block
        }

        &__select,
        &__submit {
            margin-top: 1em;
        }
    }
}
